@include for-mobile-layout {
  .details-container {
    height: auto;
    flex-direction: column;
    > * {
      padding-bottom: 10px;
      padding-top: 25px;
      border-bottom: solid 1px $clr16;
    }
    .details {
      width: 100%;
      form {
        padding: 0;
      }
    }
    .supply {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
      border-left: none;
      border-right: none;
    }
    .total-payment {
      width: 100%;
      padding-right: 0;
      .total{
        margin-bottom: 32px;
      }

      .payment{
        padding-top: 25px;
        border-top: solid 1px $clr16;
        .payment-info{
          min-height: 30px;
        }

      }
      .purchase-button {
        position: static;
        margin-bottom: 20px;
        transform: none;
      }
    }
  }
}
